

* {
  font-family: monospace; 
  color:white;
}

html,body,*{
  margin:0;
  padding:0;
}



html {
  width: 100vw;
  height:100vh;
}


body, #root{
  width:100%;
  height:100%;
}

#root {
  height:100%;
}
.App {
  text-align: center;
  width:100%;
  height:100%;
  overflow-x:hidden;
}





.background
{
  position:fixed;
  top:0;
  left:0;
  overflow:hidden;
  width:100%;
  height:100%;
  z-index:-9999;
}
.home
{
    width:100%;
    height:100%;
    display:flex;
    justify-content:flex-start;
    padding:40px;
    align-items:center;  

}




.home-text
{
  font-size:50px;
}

.home-text > * 
{
  text-align:left;
}



@media only screen and (max-width: 600px) {


  .home 
  {
    align-items:flex-start;
    padding-top:200px;
  }

  .home-text > * 
{
  text-align:center;
  width:100%;
  font-size:25px;
}



}

.qualif
{
    width:100%;
    height:100%;
    display:flex;
    justify-content:flex-end;
    padding-right:40px;
    padding-left:40px;
    align-items:center;  
    box-sizing: border-box;
}


.qualif_box
{

  width:600px;
  background-color:rgba(255,255,255,0.3);
  border-radius:3px;
  padding:10px;

}

.qualif_title
{
  font-size:30px;
}

.qualif_box_content
{
  width:100%;
  display:flex;
  flex-direction:column;
  align-items: flex-start;
  flex-wrap:wrap;
}

.qualif_box_sub_item
{
  padding-left:10px;
  text-align:left
}

.qualif_box_item
{
  text-align:left
}

@media only screen and (max-width: 600px) {


  .qualif
  {
    height:auto;
    padding:40px;
    justify-content: center;
  }

  .qualif_box
  {
    width:100%;
  }
}
.projects
{
    width:100%;
    height:100%;
    display:flex;
    justify-content:flex-start;
    padding-left:40px;
    padding-right:40px;
  
    align-items:center;  
    box-sizing: border-box;
}


.projects_box
{

  width:600px;
  background-color:rgba(255,255,255,0.3);
  border-radius:3px;
  padding:10px;
  

}

.projects_title
{
  font-size:30px;
}

.projects_box_content
{
  width:100%;
  height:100%;
  display:flex;
  flex-direction:column;
  align-items: flex-start;
  flex-wrap:wrap;
}

.projects_box_sub_item
{
  padding-left:10px;
  text-align:left;
  text-overflow: wrap;
}

.projects_box_item
{
  text-align:left;
  text-overflow: wrap;
}


@media only screen and (max-width: 600px) {


  .projects
  {
    height:auto;
    padding:40px;
    justify-content: center;
  }

  .projects_box
  {
    width:90%;
  }

  .project_link
  {
    font-size:10px;
  }

}
